import { UserManager, WebStorageStateStore, Log } from "oidc-client";

const config = {
  authority: process.env.VUE_APP_IDS_URL,
  client_id: "portal.spa",
  redirect_uri: process.env.VUE_APP_SPA_PORTAL_URL + "/callback.html",
  silent_redirect_uri: process.env.VUE_APP_SPA_PORTAL_URL + "/silent.html",
  response_type: "id_token token",
  scope: "openid profile api.gateway",
  post_logout_redirect_uri: process.env.VUE_APP_SPA_PORTAL_URL,
  userStore: new WebStorageStateStore({ store: window.localStorage }),
  automaticSilentRenew: true
};

const mgr = new UserManager(config);

mgr.events.addAccessTokenExpiring(function() {
  console.log("token expiring");
});

mgr.events.addAccessTokenExpired(function() {
  console.log("token expired");
});

mgr.events.addSilentRenewError(function(e) {
  console.log("silent renew error", e.message);
});

mgr.events.addUserLoaded(function(user) {
  console.log("user loaded", user);
  mgr.getUser().then(function() {
    console.log("getUser loaded user after userLoaded event fired");
  });
});

mgr.events.addUserUnloaded(function(e) {
  console.log("user unloaded");
  //this.manager.signinRedirect()
  //window.location.replace("/");
});

Log.logger = console;

export default {
  manager: mgr,
  // login challenge
  authenticated(challenge = false, callbackFunc) {
    this.manager.getUser().then(user => {
      if (user) {
        console.log("User logged in", user.profile);
        // this.manager.startSilentRenew()
        callbackFunc(true);
      } else {
        console.log("User not logged in");
        if (challenge) {
          console.log("Challenging");
          this.manager
            .signinRedirect({
              state: {
               redirectUrl : window.location.hash
              }
            })
            .then(callbackFunc(false));
        } else {
          callbackFunc(false);
        }
      }
    });
  },
  logout() {
    this.manager.signoutRedirect();
  },
  login() {
    this.manager.signinRedirect();
  },
  accessToken(cbFunc) {
    this.manager.getUser().then(user => {
      cbFunc(user.access_token);
    });
  }
};
